
.Board {
    margin: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.Board, .Board * {
    box-sizing: border-box;
}

.Board > div {
    position: relative;
}

.row {
    margin: 0 auto;
    padding: 0;
    font-size: 0;
}

.row * {
    font-size: 0;
}

.board-object {
    position: relative;
    margin: 0;
    padding: 0;
    border: 0;
    display: inline-block;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    animation-duration: 1.5s;
    animation-iteration-count: infinite;
    width: 100%;
    height: 100%;
}

.board-object > * {
    position: absolute;
    top: 0;
    left: 0;
    margin: 0;
    padding: 0;
    border: 0;
    display: inline-block;
    background-size: 100% 100%;
    background-repeat: no-repeat;
    /* animation-duration: 1.5s;
    animation-iteration-count: infinite; */
    width: 100%;
    height: 100%;
}

.tank {
    position: absolute;
    margin: 0;
    padding: 0;
    border: 0;
    display: inline-block;
    background-size: 100% 100%;
}

.laser {
    position: absolute;
    margin: 0;
    padding: 0;
    border: 0;
    display: inline-block;
    background-color: #0df90a;
    border: 1px solid black;
}

